function promisify(f, manyArgs = false) {
    return function(...arg) {
        return new Promise((res, rej) => {
            function call(err, ...result) {
                if (err) {
                    rej(err)
                } else {
                    res(manyArgs ? result : result[0])
                }
            }
            arg.push(call)
            f.call(this, arg)
        })
    }
}

