let user = {
    name: 'andreypost'
}

fetch(`https://api.github.com/users/${user.name}`)
.then((response) => {
    return response.json()
})
.then((text) => {
    let src = text.avatar_url
    let base = document.querySelector('.setImgGl > img').src
    document.querySelector('.setImgGl > img').src = src
    setTimeout(() => document.querySelector('.setImgGl > img').src = base, 3000)
})


